import React from "react";
import PageWithSidebarStructure from 'gui/components/pageWithSidebarStructure';
import PredefinedMenuList
  from 'gui/pages/mainRouter/components/navigation/components/authenticationNavigator/components/loggedIn/components/menuList';
import {Route, Switch} from 'react-router';
import {linksMap, USER_TYPE_ADMIN} from 'data/utils/constants';
import DashboardContainer from 'data/redux/containers/dashboardContainer';
import {ReactComponent as adminShield} from 'assets/img/adminShield.svg';
import {ReactComponent as adminUsers} from 'assets/img/adminUsers.svg';
import {ReactComponent as adminReports} from 'assets/img/adminReports.svg';
import {ReactComponent as adminSettings} from 'assets/img/adminSettings.svg';
import {ReactComponent as adminSupport} from 'assets/img/adminSupport.svg';
import {ReactComponent as exitDoor} from 'assets/img/exitDoor.svg';
import AdminProductsContainer from 'data/redux/containers/adminProductsContainer';
import AdminEditProductContainer from 'data/redux/containers/adminEditProductContainer';
import styles from 'gui/pages/controlRouter/ControlRouter.module.scss';

const AdminRouter = () => {
  return (<PageWithSidebarStructure
      navigation={<PredefinedMenuList
        className={`${styles.sideBarControlNavigationContainer} hideWhen767`}
        menuList={[
          {path: linksMap.adminProducts, userType: USER_TYPE_ADMIN, label: "מוצרים", Icon: adminShield},
          {path: linksMap.adminUsers, userType: USER_TYPE_ADMIN, label: "משתמשים", Icon: adminUsers},
          {path: linksMap.adminReports, userType: USER_TYPE_ADMIN, label: "דוחות", Icon: adminReports},
          {path: linksMap.adminSettings, userType: USER_TYPE_ADMIN, label: "הגדרות", Icon: adminSettings},
          {path: linksMap.adminSupport, userType: USER_TYPE_ADMIN, label: "תמיכה", Icon: adminSupport},
          {path: linksMap.logout, label: "התנתק", onlyClick: true, Icon: exitDoor},
        ]}
      />}>
      <Switch>
        <Route exact path={linksMap.adminProducts}>
          <AdminProductsContainer/>
        </Route>
        <Route exact path={linksMap.adminUsers}>
          <DashboardContainer/>
        </Route>
        <Route exact path={linksMap.adminReports}>
          <DashboardContainer/>
        </Route>
        <Route exact path={linksMap.adminSupport}>
          <DashboardContainer/>
        </Route>
        <Route path={linksMap.adminEditAd}>
          <AdminEditProductContainer/>
        </Route>
      </Switch>
    </PageWithSidebarStructure>
  );
};

AdminRouter.defaultProps = {};

export default AdminRouter;
