import {connect} from 'react-redux'
import {AuthenticatedMapDispatchToProps, AuthenticatedMapStateToProps} from 'data/redux/containers/commons/Commons';
import AdminEditProduct from 'gui/pages/adminRouter/pages/adminEditProduct';
import {IProduct} from 'data/interfaces/products';
import {getUpdateProductActionCreator} from 'data/redux/actions/productsActions';
import {linksMap} from 'data/utils/constants';
import {navigateIfNotThere} from 'data/redux/actions/navigationActions';

const mapStateToProps = (state, ownProps) => {
  return {
    ...AuthenticatedMapStateToProps(state),
    ...ownProps,
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
    onSubmit: (product: IProduct) => dispatch(getUpdateProductActionCreator(product))
      .then(r => {
        dispatch(navigateIfNotThere(linksMap.adminProducts));
        return r;
      })
      .catch(e => {
        
        return Promise.resolve({error: true});
      }),
    setUrl: (path: string) => dispatch(navigateIfNotThere(path)),
    ...AuthenticatedMapDispatchToProps(dispatch)
  };
};


const AdminEditProductContainer = connect(mapStateToProps, mapDispatchToProps)(AdminEditProduct);

export default AdminEditProductContainer;
