import {connect} from 'react-redux'
import {
  allProductsLimitSelector,
  allProductsSelector,
  allProductsTotalSelector
} from 'data/redux/selectors/productSelector';
import {AuthenticatedMapDispatchToProps, AuthenticatedMapStateToProps} from 'data/redux/containers/commons/Commons';
import {getLoadAllProductsActionCreator} from 'data/redux/actions/productsActions';
import {USER_TYPE_ADMIN} from 'data/utils/constants';
import AdminProducts from 'gui/pages/adminRouter/pages/adminProducts';
import {getQueryParamsSelector} from 'data/redux/selectors/routerSelector';
import {navigateSetQueryParams} from 'data/redux/actions/navigationActions';

const mapStateToProps = (state, ownProps = {userType: USER_TYPE_ADMIN}) => {
  return {
    productsByMe: allProductsSelector(state),
    limit: allProductsLimitSelector(state),
    total: allProductsTotalSelector(state),
    queryParams: getQueryParamsSelector(state),
    ...AuthenticatedMapStateToProps(state, ownProps),
    ...ownProps,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    loadAllProductProductsByMe: (options?: { query?: any }) => dispatch(getLoadAllProductsActionCreator(options)),
    setQueryParams: (queryParamsObject) => dispatch(navigateSetQueryParams(queryParamsObject)),
    ...AuthenticatedMapDispatchToProps(dispatch)
  };
};

const AdminProductsContainer: any = connect(mapStateToProps, mapDispatchToProps)(AdminProducts as any);

export default AdminProductsContainer;
