import React from 'react'
import {TableFooter as MUITableFooter, TablePagination, TableRow} from '@material-ui/core';
import TablePaginationActions from '@material-ui/core/TablePagination/TablePaginationActions';
import {trimNumberMinMax} from 'data/utils/calculators';


export interface ITableBody {
  columns: any;
  pageIndex: number;
  pageSize: number;
  pageCount: number;
  gotoPage: any;
  rows: any[];

  total: number;
  limit: number;
  setLimit: any;
  setPage: any;
}


const TableFooter = ({
                       columns,
                       total,
                       limit,
                       pageIndex,
                       pageSize,
                       setPage,
                       gotoPage,
                       pageCount,
                       setLimit,
                       rows
                     }: ITableBody) => {
  return (
    <MUITableFooter>
      <TableRow>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25, 50, 100]}
          colSpan={columns.map(c => c.columns.length).reduce((a, b) => a + b, 0)}
          count={total || rows.length}
          rowsPerPage={limit || pageSize}
          page={pageIndex}
          onChangePage={(event, b) => {
            const newPage = trimNumberMinMax(b, 0, pageCount - 1);
            if (typeof setPage === 'function') {
              setPage(newPage);
            }
            gotoPage(newPage);
          }}
          onChangeRowsPerPage={(a) => {
            if (typeof setLimit === 'function') {
              setLimit(parseInt(a.target.value) || 10);
            }
          }}
          ActionsComponent={TablePaginationActions}
          labelRowsPerPage="כמות שורות להציג"
          labelDisplayedRows={({from, to, count}) => {
            return [from,
              "עד",
              to,
              "מתוך",
              count
            ].join(" ");
          }}
        />
      </TableRow>
    </MUITableFooter>
  );
}
TableFooter.defaultProps = {};
export default TableFooter;
