import React from 'react';
import {Chip, Input, MenuItem, Select} from '@material-ui/core';

interface IDefaultColumnFilter {
  column: { filterValue, setFilter, preFilteredRows, id };
}

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const SelectColumnFilter = (options: { label: string, value: number }[], onChange: (columnFilter: IDefaultColumnFilter, e: any, b?: any, skipLoad?: boolean)
  => void = ({
               column: {
                 filterValue,
                 preFilteredRows,
                 setFilter
               },
             }, e: any) => {
  setFilter(e.target.value || undefined);

}) => ({
         column: {filterValue, setFilter, preFilteredRows, id},
       }: IDefaultColumnFilter) => {
  // Render a multi-select box

  return (
    <Select
      multiple
      defaultValue={filterValue || []}
      onChange={(e, b, skipLoad?) => {
        onChange({
          column: {filterValue, setFilter, preFilteredRows, id},
        }, e, b, skipLoad);
      }}
      input={<Input id="select-multiple-chip"/>}
      renderValue={(selected) => (
        <div>
          {(selected as string[]).map((value: any) => (
            <Chip key={value} label={options.find((v) => v.value === value).label} style={{display: 'block'}}/>
          ))}
        </div>
      )}
      MenuProps={MenuProps}
    >
      {options.map(({label, value}, i) => (
        <MenuItem key={label} value={value} title={label}>
          {label}
        </MenuItem>
      ))}
    </Select>
  );
}

export default SelectColumnFilter;
