import React, {useEffect, useRef} from 'react'
import {useFilters, usePagination, useSortBy, useTable} from 'react-table'
import {Table as MUITable} from '@material-ui/core';
import TableHead from './components/tableHead';
import TableBody from './components/tableBody';
import TableFooter from './components/tableFooter';
import {filterTypes} from './filters';
import TextColumnFilter from './components/tableHead/components/textColumnFilter';

//import {columns, data} from 'gui/components/smartTable/tableSample';
interface IColumn {
  Header?: any;
  accessor?: any;
  filter?: any;
  Filter?: any;
  defaultSort?: boolean;
  defaultFilter?: string;
}

interface IColumns {
  id: string;
  Header: string;
  columns: IColumn[];
}

export interface ITable {
  columns: IColumns[];
  data: any[];
  //loading?: boolean;
  loadingColumn?: boolean;
  pageNumber?: number;
  limit: number;
  total: number;
  setPage?: (page: number) => void;
  setLimit?: (limit: number) => void;
}

const defaultColumn = {
  // Let's set up our default Filter UI
  Filter: TextColumnFilter(),
};

const SmartTable = ({columns, data, loadingColumn, limit, total, pageNumber, setPage, setLimit}: ITable) => {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    page,
    state: {pageIndex, pageSize, sortBy},
    pageCount,
    gotoPage,
  } = useTable(
    {
      columns,
      data,
      defaultColumn, // Be sure to pass the defaultColumn option
      filterTypes,
      initialState: {
        pageIndex: 0,
        sortBy: columns
          .map(({columns}) => columns)
          .flat()
          .filter(column => typeof column.defaultSort === 'boolean')
          .map(column => ({
            id: column.accessor,
            desc: column.defaultSort,
          })),
        filters: columns
          .map(({columns}) => columns)
          .flat()
          .filter(column => column.defaultFilter && (typeof column.defaultFilter === 'string' || Array.isArray(column.defaultFilter)))
          .map(column => ({
            id: column.accessor,
            value: column.defaultFilter,
          })),
      },
      manualFilters: true,
      autoResetFilters: true,
      autoResetSortBy: false,
      autoResetPage: true,
      disableSortRemove: true,
      manualSortBy: true,
      disableMultiSort: true,
      disableMultiRemove: true,
      manualPagination: true,
      pageCount: Math.ceil(total / limit),
    },
    useFilters, // useFilters
    useSortBy,
    usePagination,
  );
  const firstRender = useRef(true);
  useEffect(() => {
    const _columns = columns.map(({columns}) => columns).flat();
    /**
     * Set Default filter to state(in AdminProducts)
     */
    if (firstRender.current) {
      /**
       * Must provide a basic column so it doesn't error
       */
      const defaultColumn = {
        column: {
          setFilter: () => {
          }
        }
      };
      _columns
        .forEach(column => {
          if (column.defaultFilter && column.Filter && typeof column.Filter === 'function') {
            column.Filter(defaultColumn).props.onChange({target: {value: column.defaultFilter}}, null, true);
          }
        });
    }
    /**
     * Set Default sort to state(in AdminProducts)
     * but also performs the actual sorting
     */
    sortBy.forEach(colSort => {
      const column: any = _columns.find(column => column.accessor === colSort.id);
      if (column && typeof column.sortType === 'function') {
        column.sortType(null, null, colSort.id, colSort.desc);
      }


    });

    firstRender.current = false;

  }, [sortBy, columns]);
  return (
    <MUITable stickyHeader {...getTableProps()}>
      <TableHead headerGroups={headerGroups} loadingColumn={loadingColumn}/>
      <TableBody page={page} prepareRow={prepareRow} getTableBodyProps={getTableBodyProps}/>
      <TableFooter columns={columns}
                   gotoPage={gotoPage}
                   limit={limit}
                   pageCount={pageCount}
                   pageIndex={pageNumber !== undefined ? pageNumber : pageIndex}
                   pageSize={pageSize}
                   rows={rows}
                   setLimit={setLimit}
                   setPage={setPage}
                   total={total}/>
    </MUITable>
  );
}
SmartTable.defaultProps = {
  columns: [],
  data: [],
  loading: false,
  loadingColumn: false,
  limit: 1,
  total: 1,
};
export default SmartTable;
