export const filterGreaterThan = (rows, id, filterValue) => {
  return rows.filter(row => {
    const rowValue = row.values[id]
    return rowValue >= filterValue
  })
};
filterGreaterThan.autoRemove = val => typeof val !== 'number'


export const filterTypes = {
  text: (rows, id, filterValue) => {
    const f = String(filterValue).toLowerCase();
    return rows.filter(row => {
      const rowValue = row.values[id]
      return rowValue !== undefined
        ? String(rowValue)
          .toLowerCase()
          .includes(f)
        : true
    })
  },
  number: (rows, id, filterValue) => {
    const f = String(filterValue.replace(/\D/g, '')).toLowerCase();
    return rows.filter(row => {
      const rowValue = row.values[id].replace(/\D/g, '');
      return rowValue !== undefined
        ? String(rowValue)
          .toLowerCase()
          .includes(f)
        : true
    })
  },
};
