import TextField from '@material-ui/core/TextField/TextField';
import React from 'react';

interface IDefaultColumnFilter {
  column: { filterValue, preFilteredRows, setFilter, defaultValue };
}


const TextColumnFilter = (onChange: (columnFilter: IDefaultColumnFilter, e: any) => void = ({
                                                                                              column: {
                                                                                                filterValue,
                                                                                                preFilteredRows,
                                                                                                setFilter
                                                                                              },
                                                                                            }, e: any, skipLoad?: boolean) => {
  setFilter(e.target.value || undefined);

}) => ({
         column: {filterValue, preFilteredRows, setFilter, defaultValue},
       }: IDefaultColumnFilter) => {
  return (
    <TextField
      value={filterValue || defaultValue || ''}
      onChange={(e) => {
        onChange({
          column: {filterValue, preFilteredRows, setFilter},
        } as any, e);
      }}
      placeholder={`פילטר`}
    />
  )
}


export default TextColumnFilter;
