import React from 'react'
import {TableCell, TableHead as MUITableHead, TableRow} from '@material-ui/core';
import ExpandLessRoundedIcon from '@material-ui/icons/ExpandLessRounded';
import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';
import {Else, If, Then, When} from 'react-if';

export interface ITableHead {
  headerGroups: any;
  loadingColumn?: boolean;
}


const TableHead = ({headerGroups, loadingColumn}: ITableHead) => {
  return (
    <MUITableHead>
      {headerGroups.map(headerGroup => (
        <TableRow {...headerGroup.getHeaderGroupProps()}>
          {headerGroup.headers.map(column => {
            return (
              <TableCell {...column.getHeaderProps()}>
                <span {...column.getHeaderProps(column.getSortByToggleProps())}>{column.render('Header')}</span>
                <span>
                  <When condition={!!column.isSorted}>
                    {() => (<If condition={!!loadingColumn}>
                      <Then>
                        {() => (<img alt="loading" style={{width: 20, height: 20}} src='/img/spinner.svg' loading="lazy"></img>)}
                      </Then>
                      <Else>
                        {() => (<If condition={!!column.isSortedDesc}>
                          <Then>{() => (<ExpandMoreRoundedIcon/>)}</Then>
                          <Else>{() => (<ExpandLessRoundedIcon/>)}</Else>
                        </If>)}
                      </Else>
                    </If>)}
                  </When>
                  </span>
                <div>{column.canFilter ? column.render('Filter') : null}</div>
              </TableCell>
            );
          })}
        </TableRow>
      ))}
    </MUITableHead>
  );
}
TableHead.defaultProps = {
  loadingColumn: false,
};
export default TableHead;
