import React from 'react';
import './AdminProducts.css';
import AuthenticatedPage, {IAuthenticatedPageProps} from 'gui/components/authenticatedPage/AuthenticatedPage';
import {MDBCol, MDBContainer, MDBRow} from 'mdbreact';
import {IProduct} from 'data/interfaces/products';
import LoaderContainer from 'data/redux/containers/LoaderContainer/LoaderContainer';
import ScrollToTopExtended from 'gui/components/scrollToTopExtended/ScrollToTopExtended';
import moment from 'moment';
import SmartTable from 'gui/components/smartTable/SmartTable';
import numeral from 'numeral';
import {productEndingSelector} from 'data/redux/selectors/productSelector';
import {statusToText} from 'gui/components/listPage/components/ad/components/adStatus';
import {Paper} from '@material-ui/core';
import uniqWith from 'uniq-with';
import AwesomeDebouncePromise from 'awesome-debounce-promise';
import promisifySetState from 'promisify-setstate';
import TextColumnFilter from 'gui/components/smartTable/components/tableHead/components/textColumnFilter';
import SelectColumnFilter from 'gui/components/smartTable/components/tableHead/components/selectColumnFilter';
import {allProductStates} from 'data/redux/constants/productsConstants';
import EditAdButton
  from 'gui/components/listPage/components/ad/components/adControl/components/editAdButton';
import ViewAdButton
  from 'gui/components/listPage/components/ad/components/adControl/components/viewAdButton';
import {discardObjectKeys} from 'data/utils/calculators';
import {decodeUrlComponentUntilNoChange} from 'data/utils/shortcuts';

interface IAdminProducts extends IAuthenticatedPageProps {
  productsByMe: IProduct[];
  loadAllProductProductsByMe?: (options?: any) => Promise<any>;
  queryParams?: any;
  limit?: number;
  total?: number;
  view?: (id: number) => void;
  edit?: (id: number) => void;
  delete?: (options?: { query?: any, type?: string }) => void;

  setQueryParams: (queryParamsObject?: any) => void;

}


class AdminProducts extends AuthenticatedPage<IAdminProducts> {

  state: {
    loadingColumn: false,
    joinRelations: {},
    query: {
      $skip: 0,
      $limit: 10,
      $and: [],
      $sort: {},
    },
  }
  loadAllProductProductsByMe: any;

  constructor(props: any) {
    super(props);
    this.state = {
      loadingColumn: false,
      joinRelations: {},
      query: {
        $skip: 0,
        $limit: 10,
        $and: [],
        $sort: {},
      },
    };
    this.loadAllProductProductsByMe = AwesomeDebouncePromise(this.props.loadAllProductProductsByMe, 200);
  }

  componentDidMount() {
    super.componentDidMount();
    //this.loadProductsByMe();
  }

  private startTableLoading = (skipLoading = false) => {
    return this.setState({loadingColumn: skipLoading === true ? false : true}) as any as Promise<void>;
  }
  private stopTableLoading = () => {
    return this.setState({loadingColumn: false}) as any as Promise<void>;
  }

  private setPage = (page) => {
    const state: any = this.state;
    const query = state.query;
    query.$skip = page * query.$limit;
    return Promise.resolve();
  };

  private setLimit = (limit) => {
    const state: any = this.state;
    const query = state.query;
    query.$limit = limit;
    if (query.$skip < query.$limit) {
      query.$skip = 0;
    }
    return Promise.resolve();
  };

  private setRelations(inputRelations?) {
    const state: any = this.state;
    //const relations = inputRelations ? {[Math.random()]: inputRelations} : state.joinRelations;
    // state.query.$joinRelation = `[${uniqWith(((a, b) => a === b), Object.keys(relations).map(col => relations[col]).flat()).join(",")}]`;
    /**
     * Just use all the $and and $sort queries for relation taking whats before the .  make sure they all have full database paths
     * convert object-path into $joinRelation
     */

    const relations = uniqWith(((a, b) => a === b),
      /**
       * Combine all the possible keys
       */
      [...state.query.$and
        .map(s => Object.keys(s)[0]),
        ...Object.keys(state.query.$sort)
      ]
        /**
         * Treat : as .  and then split into an array of relations
         */
        .map(s => s.replace(/:/g, ".").split("."))
        /**
         * Make sure relation is required, if its just 1 direct key, then it does not need any relation
         */
        .filter((s => s.length > 1))
        /**
         * remove the last element to get the relation path
         */
        .map(s => {
          s.pop();
          return s.join(".");
        })
        .flat());

    state.query.$joinRelation = `[${relations.join(",")}]`;
    return Promise.resolve();
  }

  private setSort = (column: string, sortType: number, ...joinRelations: string[]) => {
    const state: any = this.state;
    const query = state.query;
    query.$sort = {
      [column]: sortType,
    };
    this.props.setQueryParams({
      ...discardObjectKeys((this.props.queryParams || {}), (key, value) => key.toLowerCase().endsWith("sort")),
      [`${column}Sort`.toLowerCase()]: sortType > 0 ? true : false,
    });
    return this.setRelations(joinRelations)
      .then(() => this.setPage(0));
  }

  private setAnd = (column: string, value: any, ...joinRelations: string[]) => {
    const c = column.split(".");
    if (Array.isArray(value)) {
      const columnKey = `${c[c.length - 1]}ArrayFilter`.toLowerCase();
      this.props.setQueryParams({
        ...discardObjectKeys(this.props.queryParams, key => key === columnKey),
        ...(value.length > 0 ? {[columnKey]: value.join("-")} : {}),
      });
    } else {
      this.props.setQueryParams({
        ...this.props.queryParams,
        [`${c[c.length - 1]}Filter`.toLowerCase()]: value,
      });
    }
    const state: any = this.state;
    const query = state.query;
    query.$and = query
      .$and
      .filter((c) => !(column in c));

    if ((typeof value === 'string' && value.length) || (value && typeof value === 'object' && Object.keys(value).length)) {
      state.joinRelations[column] = joinRelations;
      if (!isNaN(value.length) && typeof value !== 'string') {
        query.$and.push({
          [column]: {
            $in: value,
          }
        });
      } else {
        query.$and.push({
          [column]: {
            $like: `%${value}%`
          }
        });
      }
    } else {
      delete state.joinRelations[column];
    }

    return this.setRelations()
      .then(() => this.setPage(0));
  }

  private textFilterQuery = (db_column, joinRelationsArray = []) => TextColumnFilter(({
                                                                                        column: {
                                                                                          filterValue,
                                                                                          preFilteredRows,
                                                                                          setFilter
                                                                                        },
                                                                                      }, e: any, skipLoad = false) => {
    const value = e.target.value;
    setFilter(value || undefined);
    this.startTableLoading(skipLoad)
      .then(() => this.setAnd(db_column, value, ...joinRelationsArray))
      .then(() => this.loadProductsByMe());
    //.then(() => setFilter(value || undefined))
    //.then(() => this.stopTableLoading());
  });

  private selectFilterQuery = (db_column, options = [], joinRelationsArray = []) => SelectColumnFilter(options, ({
                                                                                                                   column: {
                                                                                                                     filterValue,
                                                                                                                     preFilteredRows,
                                                                                                                     setFilter
                                                                                                                   },
                                                                                                                 }, e: any, b: any, skipLoad = false) => {
    const value = e.target.value;
    this.startTableLoading(skipLoad)
      .then(() => this.setAnd(db_column, value, ...joinRelationsArray))
      .then(() => skipLoad === true ? Promise.resolve() : this.loadProductsByMe())
      //  .then(() => setFilter(label || undefined))
      .then(() => skipLoad === true ? Promise.resolve() : this.stopTableLoading());
  });

  private sortQuery = (db_column, joinRelationsArray = []) => (a, b, column, sortType, skipLoad = false) => {
    this.startTableLoading(skipLoad)
      .then(() => this.setSort(db_column, sortType ? 1 : -1, ...joinRelationsArray))
      .then(() => skipLoad === true ? Promise.resolve() : this.loadProductsByMe())
      .then(() => skipLoad === true ? Promise.resolve() : this.stopTableLoading());
  };

  loadProductsByMe = (queryOptions = {}) => {
    return this.loadAllProductProductsByMe({
      ...this.state.query,
      ...queryOptions
    }).catch(() => Promise.resolve());
  };

  private columns = [
    {
      id: 'AdminProductsSmartTable',
      Header: '',
      columns: [
        {
          Header: 'מס',
          accessor: 'id',
          Filter: <div style={{paddingTop: 6, paddingBottom: 7}}>‎</div>,
        },
        {
          Header: 'מספר מוצר',
          accessor: 'productIdentifier',
          Filter: this.textFilterQuery('productIdentifier'),
          sortType: this.sortQuery('productIdentifier'),
        },
        {
          Header: 'פרטי',
          accessor: 'firstName',
          Filter: this.textFilterQuery('permissions:userDetails.firstName', ['permissions.userDetails']),
          sortType: this.sortQuery('firstName', ['permissions.userDetails']),
        },
        {
          Header: 'משפחה',
          accessor: 'lastName',
          Filter: this.textFilterQuery('permissions:userDetails.lastName', ['permissions.userDetails']),
          sortType: this.sortQuery('lastName', ['permissions.userDetails']),
        },
        {
          Header: 'מייל',
          accessor: 'email',
          Filter: this.textFilterQuery('permissions:userDetails.email', ['permissions.userDetails']),
          sortType: this.sortQuery('email', ['permissions.userDetails']),
        },
        {
          Header: 'טלפון',
          accessor: 'phone',
          Filter: this.textFilterQuery('permissions:userDetails.phone', ['permissions.userDetails']),
          sortType: this.sortQuery('phone', ['permissions.userDetails']),
        },
        {
          Header: 'תאריך פרסום',
          accessor: 'createdAt',
          sortType: this.sortQuery('createdAt'),
        },
        {
          Header: 'תאריך סיום',
          accessor: 'endingAt',
          sortType: this.sortQuery('endingAt'),
        },
        {
          Header: 'סוג',
          accessor: 'productCategory',

        },
        {
          Header: 'סוג משני',
          accessor: 'productType',
          Filter: this.textFilterQuery('typeDetails.name', ['typeDetails']),
          sortType: this.sortQuery('productType', ['typeDetails']),
        },
        {
          Header: 'עלות',
          accessor: 'askingPrice',
          Filter: this.textFilterQuery('saleDetails.askingPrice', ['saleDetails']),
          filter: 'number',
          sortType: this.sortQuery('askingPrice'),
        },
        {
          Header: 'סטאטוס',
          accessor: 'status',
          Filter: this.selectFilterQuery('status', allProductStates.map(status => ({
            label: statusToText(status),
            value: status
          }))),
          sortType: this.sortQuery('status'),
        },
        {
          Header: 'SLA',
          accessor: 'updatedAt',
          filter: 'number',
          sortType: this.sortQuery('updatedAt'),
          defaultSort: false,
        },
        {
          //   Header: <div style={{width: 25}}/>,
          accessor: 'actions',
          Filter: <div style={{paddingTop: 6, paddingBottom: 7}}>‎</div>,
        },
      ]
        .map((column: any) => {
          const filter = decodeUrlComponentUntilNoChange(this.props.queryParams[`${column.accessor}Filter`.toLowerCase()]);
          const filterArray = (decodeUrlComponentUntilNoChange(this.props.queryParams[`${column.accessor}ArrayFilter`.toLowerCase()] || ''))
            .split("-")
            .map(s => parseInt(s))
            .filter(s => s);
          const sort = this.props.queryParams[`${column.accessor}Sort`.toLowerCase()];
          if (Object.keys(this.props.queryParams || {}).find(s => s.toLowerCase().endsWith("sort"))) {
            delete column.defaultSort;
          }
          if (Object.keys(this.props.queryParams || {}).find(s => s.toLowerCase().endsWith("filter"))) {
            delete column.defaultFilter;
          }
          return {
            ...column,
            ...(sort ? {defaultSort: sort === 'true'} : {}),
            ...(filter ? {defaultFilter: filter} : {}),
            ...(filterArray && Array.isArray(filterArray) && filterArray.length > 0 ? {defaultFilter: filterArray} : {})
          };
        })

      ,
    },
  ];

  render() {
    return (
      <MDBContainer fluid className="adminProductsContainer">
        <ScrollToTopExtended scrollOnRender smooth/>
        <MDBRow>
          <MDBCol sm="8" md="9" lg="10">
            <h2 className="topRow">
              כל המוצרים באתר
            </h2>
          </MDBCol>
          <MDBCol sm="4" md="3" lg="2"/>
        </MDBRow>
        <LoaderContainer>
          <Paper elevation={0} style={{padding: 20}}>
            <SmartTable loadingColumn={this.state.loadingColumn}
                        limit={this.state.query.$limit}
                        total={this.props.total}
                        columns={this.columns}
                        pageNumber={Math.floor(this.state.query.$skip / this.state.query.$limit)}
                        setLimit={(limit) => this.setLimit(limit).then(() => this.loadProductsByMe())}
                        setPage={(page) => this.setPage(page).then(() => this.loadProductsByMe())}
                        data={
                          this.props.productsByMe
                            .map((product, index) => ({
                              id: index + 1 + this.state.query.$skip,
                              productIdentifier: product.productIdentifier,
                              firstName: product.permissions[0].userDetails.firstName,
                              lastName: product.permissions[0].userDetails.lastName,
                              email: product.permissions[0].userDetails.email,
                              phone: product.permissions[0].userDetails.phone,
                              createdAt: moment(new Date(product.createdAt)).format("DD/MM/YY HH:ss"),
                              endingAt: moment(new Date(productEndingSelector(product))).format("DD/MM/YY HH:ss"),
                              productCategory: "תיירות",
                              productType: product.typeDetails.name,
                              askingPrice: numeral(product.saleDetails ? product.saleDetails.askingPrice : 0).format("0,0"),
                              status: statusToText(product.status),
                              updatedAt: numeral(moment(new Date()).diff(new Date(product.updatedAt), 'days') + 1).format("0,0"),
                              actions: <span>
                                <EditAdButton product={product} admin={true}/>
                                <ViewAdButton product={product}/>
                              </span>,
                            }))}/>
          </Paper>
        </LoaderContainer>
      </MDBContainer>
    );
  }
}

export default promisifySetState(AdminProducts);
